import React, { Component } from 'react';
import { Route, NavLink, BrowserRouter } from "react-router-dom";
import Upload from './Upload';
import MainBody from './MainBody';


class NavList extends Component {
    render() {
        return (
            <BrowserRouter>
                <div className='list_menu'>
                     <ul>
                          <li><NavLink className='nav_button' to='/MyInstagram'>Home</NavLink></li>
                          <li className="li2"><input type='text' className='nav_input' placeholder='Search' /></li>
                          <li><NavLink className='nav_button' to='/upload'>Blog</NavLink></li>
                     </ul>
                  <Route path='/MyInstagram' component={MainBody} exact />
                  <Route path='/upload' component={Upload} />
                </div>
            </BrowserRouter>
        )
    }
}

export default NavList;


