import React, { Component } from 'react';
import Modal from 'react-modal';
import './App.css';
import photos from './photos';
import LoginModal from './Login-in';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    width                 : '960px',
    height                : '500px'
  }
};

class MainBody extends Component {
    render() {
        return (
            <div>
                <LoginModal />
                <ProfileInfo items={photos} />
                <MainBodyContent items={photos}/>
                <Footer />
            </div>
        )
    }
}

class ProfileInfo extends Component {
    render() {
        return (
            <div className='ProfileName'>
            <img alt='avatar' className='profile_avatar' src='/img/pixel.jpg' />
                <ul>
                  <li><a> </a></li><br />
                  <li>{this.props.items.length} </li>
                  <li></li>
                </ul>
            </div>
        )
    }
}

class MainBodyContent extends Component {
    constructor() {
        super();
        this.state = {
            modalIsOpen: false
        };

        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    openModal() {
        this.setState({modalIsOpen: true});
    }

    closeModal() {
        this.setState({modalIsOpen: false});
    }
    render() {

        let images = this.props.items.map((item,index) =>
             <img
             alt={index}
             src={item}
             onClick={this.openModal}
             key={index} width='291px' height='291px' />
             )
        
       let checkEvent = (event) => {
            for (var i = 0; i < images.length; i++) {
                if (event.target = images[i]) {
                    return (
                        <div>
                            { images[i] }
                        </div>
                    );
                }
            }
        }
        return (
            <div className='content'>
             { images }
            <Modal
               isOpen={this.state.modalIsOpen}
               onAfterOpen={this.afterOpenModal}
               onRequestClose={this.closeModal}
               style={customStyles}
               contentLabel="Example Modal"
             >
            <div>
                { checkEvent }
            </div>
            <input alt='foto' className='close_modal' type='image' src={require('./Assets/close.png')} onClick={this.closeModal} />
            <Add />
            </Modal>
            </div>
        )
    }
}

class Add extends Component {
  constructor() {
    super();
      this.state = {
      textIsEmpty: true
      };
  }

  onBtnClickHandler(e) {
    e.preventDefault();
      
    this.setState({textIsEmpty: true});
  }

  onFieldChange(fieldName, e) {
    if (e.target.value.trim().length > 0) {
      this.setState({[''+fieldName]:false})
    } else {
      this.setState({[''+fieldName]:true})
    }
  }

  render() {
    let textIsEmpty = this.state.textIsEmpty;
    return (
      <form className='add cf'>
        <input
          className='add__text'
          onChange={this.onFieldChange.bind(this, 'textIsEmpty')}
          placeholder=' ...'
          ref='text'
        />

        <button
          className='add__btn'
          onClick={this.onBtnClickHandler.bind(this)}
          ref='alert_button'
          disabled={textIsEmpty}
          >
          
        </button>
      </form>
    );
  }
};

const Footer = () => {
        return (
         <footer>
            <ul>
             <li> </li>
             <li></li>
             <li></li>
             <li></li>
             <li>api</li>
             <li></li>
             <li></li>
             <li></li>
             <li></li>
             <li></li>
             <li></li>
             <li></li>
            </ul>
        </footer>
    )
}

export default MainBody;


