import React, { Component } from 'react';
import Modal from 'react-modal';

const customStyles = {
  content : {
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)',
    width                 : '500px',
    height                : '300px',
    paddingTop            : '100px',
    border                : '3px solid gray',
    borderRadius          : '5px'
  }
};

class LoginModal extends Component {
        constructor() {
        super();
        this.state = {
            modalIsOpen: true
        };
        this.closeModal = this.closeModal.bind(this);
    }
    closeModal() {
        this.setState({modalIsOpen: false});
    }
    
    handleChangeLogin(event) {
      this.setState({title: event.target.value})
    }
    handleChangePassword(event) {
      this.setState({pas: event.target.value})
    }
    

    checkDataHandler = (event) => {
        if (this.state.title !== 'Admin' && this.state.pas !== '12345') {
           return alert('   !');
        } else {
            this.closeModal();
        }
    }
    render () {
        return (
            
            <Modal
               isOpen={this.state.modalIsOpen}
               onAfterOpen={this.afterOpenModal}
               style={customStyles}
               contentLabel="Example Modal">
             <div className='login_field'>
                <input type='text' placeholder='Login' value={this.state.title} onChange={this.handleChangeLogin.bind(this)} /><br />

                <input type='password' placeholder='Password' className='start_password' value={this.state.pas} onChange={this.handleChangePassword.bind(this)} /><br />
                    
                <a onClick={this.checkDataHandler}>Log-in</a><br />
                <p>Login: "Admin"<br />Password: "12345"</p>
              </div>
            </Modal>
            
        )
    }
}

export default LoginModal


